inal de la evaluación de la comunidad con un grupo de personas con antecedentes similares y, a menudo, conocedores de un tema. Aunque la discusión puede centrarse en un tema específico, quienes forman parte del grupo pueden abordar libre y espontáneamente otros.")])]),_c('div',{staticClass:"col-xl-6 align-self-center"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-23.png"),"alt":"Entrevistas con grupos focales"}})])])])]),_c('div',{staticClass:"tarjeta color-acento-botones fnd-5 p-4 t5",attrs:{"titulo":"Mapeo comunitario"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-6 align-self-center"},[_c('p',[_vm._v("Es una herramienta útil para visualizar los recursos y servicios de una comunidad como clínicas, colegios, fuentes de agua, refugios; Asimismo, permite la identificación de riesgos, tales como áreas inundadas y amenazadas para la salud, señalando grupos y zonas vulnerables.")])]),_c('div',{staticClass:"col-xl-6 align-self-center"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-24.png"),"alt":"Mapeo comunitario"}})])])])]),_c('div',{staticClass:"tarjeta color-acento-botones fnd-5 p-4 t5",attrs:{"titulo":"Cuestionarios"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-6 align-self-center"},[_c('p',[_vm._v("Es una herramienta que permite obtener más información sobre un tema determinado.")]),_c('p',[_vm._v("Posibilita la participación de la comunidad y su acceso a conocimientos actualizados sobre sus recursos y necesidades.")])]),_c('div',{staticClass:"col-xl-6 align-self-center"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-25.png"),"alt":"Cuestionarios"}})])])])]),_c('div',{staticClass:"tarjeta color-acento-botones fnd-5 p-4 t5",attrs:{"titulo":"Entrevistas<br> semiestructuradas"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-xl-6 align-self-center"},[_c('p',[_vm._v("Son entrevistas que incluyen preguntas guía elaboradas anticipada